import React, { Component } from 'react';
import './App.css';
import { CardList } from "./components/card-list/card-list.component";
import { SearchBox } from './components/search-box/search-box.component';
import Title from './components/title/title';
import Button from '@material-ui/core/Button';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      monsters: [],
      searchField: '',
      monsterType: 1
    };
  }
  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(response => response.json())
      .then(users => this.setState({ monsters: users }));
  }

  render() {
    const style = {
      background: 'linear-gradient(180deg, #4080bf 10%, rgba(0, 128, 128, 1) 90%)',
      margin: '20px 6px',
      border: 0,
      color: '#white',
      height: 30,
      
      padding: '0 20px',
    };
    //Destructuring 
    const { monsters, searchField, monsterType } = this.state;
    // to je isto kao
    /*const monsters = this.state.monsters;
    const searchField = this.state.searchField;
    */
    const filteredMonsters = monsters.filter(monster =>
      monster.name.toLowerCase().includes(searchField.toLowerCase()));


    return (
      <div className="App">
        
        <Title />
        <SearchBox
          placeholder='Search ...'
          handleChange={e => this.setState({ searchField: e.target.value })}
        /><br></br>
        <Button variant="contained" color="primary" style={style}
          onClick={() => this.setState({ monsterType: 1 })}>
          Robots
        </Button>
        <Button variant="contained" color="primary" style={style}
          onClick={() => this.setState({ monsterType: 2 })}>
          Monsters
        </Button>
        <Button variant="contained" color="primary" style={style}
          onClick={() => this.setState({ monsterType: monsterType + 1 })}>
          Explore
        </Button>
        <CardList monsters={filteredMonsters} monsinjor={monsterType} />
      </div>
    );
  }
}

export default App;
